import { Component, OnInit } from '@angular/core';
// import { AppServices } from '../app.service';
import * as au from './all-units';

@Component({
  selector: 'app-army-table',
  templateUrl: './army-table.component.html',
  styleUrls: ['./army-table.component.css']
})
export class ArmyTableComponent implements OnInit {
  unitList = [];
  raceFilter = "ALL";
  totalUnits = 0;
  raceLogo = new Map<number, string>();
  Math: any;
  constructor() {
    // this.appService.getJSON('./assets/all_units.json')
    //   .subscribe((res) => {
    //     console.log("after reading");
    //     this.unitList = res;
    //   });
    this.Math = Math;
    this.unitList = au.all_units;
    this.raceLogo.set(1, "ALL");
    this.raceLogo.set(101, "panoceania");
    this.raceLogo.set(201, "yujing");
    this.raceLogo.set(301, "ariadna");
    this.raceLogo.set(401, "haqqislam");
    this.raceLogo.set(501, "nomadas");
    this.raceLogo.set(601, "ec");
    this.raceLogo.set(701, "aleph");
    this.raceLogo.set(801, "tohaa");
  }

  ngOnInit() {
  };
  sort = {
    column: '',
    descending: false
  };
  changeSorting = function (column) {
    if (this.sort.column == column) {
      this.sort.descending = !this.sort.descending;
    } else {
      this.sort.column = column;
      this.sort.descending = false;
    }
  };
  getTotalUnit(): any {
    return this.unitList.filter(x => {
      if (this.raceFilter == "ALL")
        return true
      else
        return this.raceLogo.get(x.RaceID) == this.raceFilter
    }).length;
  }
}



